

(function () {
var html = document.documentElement;
html.style.fontSize = html.clientWidth/6.4 + "px";
})();

import Scroller from 'vux/src/components/scroller/index.vue'


export default {
data(){
return{
hotMove:[],
freeMove:[],
fastMove:[]
}
},
components:{
Scroller
},
created(){
let url1 = 'https://api.douban.com/v2/movie/in_theaters?apikey=0b2bdeda43b5688921839c8ecb20399b&city=%E4%B8%8A%E6%B5%B7&start=0&count=100&client=somemessage&udid=dddddddddddddddddddddd';
let url2 = 'https://api.douban.com/v2/movie/new_movies?apikey=0b2bdeda43b5688921839c8ecb20399b&city=%E4%B8%8A%E6%B5%B7&start=0&count=100&client=somemessage&udid=dddddddddddddddddddddd'
let url3 = 'https://api.douban.com/v2/movie/top250?apikey=0b2bdeda43b5688921839c8ecb20399b&city=%E4%B8%8A%E6%B5%B7&start=0&count=100&client=somemessage&udid=dddddddddddddddddddddd'

this.$jsonp(url1).then(data =>{
data.subjects.forEach(item =>{
if(this.hotMove.length == 8){
return
}
this.hotMove.push(item);
})
});
this.$jsonp(url2).then(data =>{
data.subjects.forEach(item =>{
if(this.freeMove.length == 8){
return
}
this.freeMove.push(item);
})
});
this.$jsonp(url3).then(data =>{
data.subjects.forEach(item =>{
if(this.fastMove.length == 8){
return
}
this.fastMove.push(item);
})
})
}
}



