
(function () {
var html = document.documentElement;
html.style.fontSize = html.clientWidth/6.4 + "px";
})();

import Panel from 'vux/src/components/panel/index.vue'

import fmlist from '../fmlist/fmlist.vue'

export default {
data(){
return{
items:[],
}
},
components:{
fmlist:fmlist
},
methods:{
sub(str){
let arr = str.split(' ');
if(arr.length > 1){
return arr[0];
}
return str
},
href(str){
let arr = str.split('http');
if(arr.length > 1){
return 'http'+arr[1];
}
},
play(index){
let complain = document.querySelectorAll('.complain');
if(complain[index].style.display == 'block'){
complain[index].style.display = 'none';
}else {
complain[index].style.display = 'block'
}
},
setCard(item){
this.$store.commit('info',{item});
this.$router.push({path:'/fmlist'});
}
},
created(){
let url = 'https://m.douban.com/rexxar/api/v2/status/anonymous_timeline?max_id=&ck=&for_mobile=1';
this.$jsonp(url).then(item=>{
this.items = item.items;
console.log(this.items[6].status.images[0].normal.url);
})
}
}



