
(function () {
var html = document.documentElement;
html.style.fontSize = html.clientWidth/6.4 + "px";
})();

import Scroller from 'vux/src/components/scroller/index.vue'

export default {
data(){
return{
fiction:{},
nonfiction:{},
mar:{}

}
},
components:{
Scroller
},
computed:{
name(){
return this.fiction.subject_collection;
}

},
created(){
let url1 = 'https://m.douban.com/rexxar/api/v2/subject_collection/book_fiction/items?os=ios&start=0&count=8&loc_id=0&_=0';
let url2 = 'https://m.douban.com/rexxar/api/v2/subject_collection/book_nonfiction/items?os=ios&start=0&count=8&loc_id=0&_=0';
let url3 = 'https://m.douban.com/rexxar/api/v2/subject_collection/market_product_book_mobile_web/items?os=ios&start=0&count=8&loc_id=0&_=0';

this.$jsonp(url1).then(data =>{
this.fiction = data;
});
this.$jsonp(url2).then(data =>{
this.nonfiction = data;
});
this.$jsonp(url3).then(data =>{
this.mar = data;
});
},
methods:{
ficname(name){
let ficname='';
ficname = name.substr(0,4)+name.substr(8)+' | '+name.slice(5,8);
return ficname;

},
nonname(name){
let nonname = '';
nonname = name.substr(0,4)+name.substr(8)+' | '+name.slice(5,9);
return nonname;
},
marname(name){
let marname = '';
marname = name.substr(0,4);
return marname;
}
}
}



