  import Scroller from "../../../node_modules/vux/src/components/scroller/index.vue";
  import LoadMore from "../../../node_modules/vux/src/components/load-more/index.vue";

  (function () {
    var html = document.documentElement;
    html.style.fontSize = html.clientWidth/6.4 + "px";
  })();

//  import 'whatwg-fetch'

  export default {
    components: {
      LoadMore,
      Scroller,
    },
    data(){
      return {
        data: {},
        isLoad:false,
        month: 1,
        day: 1,
        arrMonth: [1,3,5,7,8,10,12]
      }
    },
    computed:{
      src(){
        return src;
      }
    },
    created(){
      let date = new Date();
      function toDb(nub) {
        return nub < 10?'0'+nub : ''+nub;
      }
       this.month = toDb(date.getMonth()+1);
      this.day = toDb(date.getDate()+1);
      console.log(this.month)
      let time = date.getFullYear() + '-'+this.month+'-'+this.day;
      let src = 'https://m.douban.com/rexxar/api/v2/recommend_feed?alt=json&next_date='+time+'&loc_id=108288&gender=&birthday=&udid=9fcefbf2acf1dfc991054ac40ca5114be7cd092f&for_mobile=1';
      let myHeaders = new Headers({
        'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json; charset=utf-8'
      });
//      fetch(src,{
//        headers:myHeaders,
//        mode: 'no-cors'
//      }).then(response =>{
//        return response.json();
//      }).then(data => {
//        console.log(data)
//      })
      this.$jsonp(src).then(data =>{
        this.data = data.recommend_feeds;
      })
    },
    methods:{
      load(){
        let date = new Date();
        if(this.day == '01'){
          this.month--;
          this.arrMonth.forEach(item =>{
            if(this.month == item){
              this.day = '31';
              return
            }
          });
          this.day = '30'
        }else {
          this.day--;
        }
        let time = date.getFullYear() + '-'+this.month+'-'+this.day;
        let src = 'https://m.douban.com/rexxar/api/v2/recommend_feed?alt=json&next_date='+time+'&loc_id=108288&gender=&birthday=&udid=9fcefbf2acf1dfc991054ac40ca5114be7cd092f&for_mobile=1';
        this.$jsonp(src).then(data =>{
          this.data.concat(data.recommend_feeds);
          console.log(this.data);
        })

      }
    }
  }



